@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:400,700);
#root {
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  font-family:  sans-serif;
  font-weight: 400;
  font-family: "Nunito Sans", sans-serif;
}

.App {
  height: 100%;
  display: flex;
  color: rgba(189, 189, 192, 1);
}
.Main {
  /* make main fill up remaining space in the viewport */
  flex: 1 1;
  /* background-color: rgba(25, 25, 27, 1); */
  background-color: #fff;
  height: 100%;
}
.Empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.Empty__name {
  color: #6f6f6f;
}
.Empty__status,
.Empty__info {
  padding: 1rem;
}
.Empty__status {
  color: #6f6f6f;
  border-bottom: 1px solid rgba(255, 255, 255, 0.7);
}
.Empty__img {
  border-radius: 50%;
  margin: 2rem 0;
}
.Empty__btn {
  padding: 1rem;
  margin: 1rem 0;
  font-weight: bold;
  font-size: 1.2rem;
  border-radius: 30px;
  outline: 0;
}
.Empty__btn:hover {
  background: rgba(255, 255, 255, 0.7);
  cursor: pointer;
}
.Header {
  padding: 1rem 2rem;
  text-align: left;
  border-bottom: 1px solid rgba(189, 189, 192, 0.2);
}
.Header__name {
  color: #525252;
  font-size: 1.5rem;
  margin: 0;
}

.Header__status {
  margin: 0.5rem 0;
  color: #a0a0a0;
}
.Chats {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  margin: 0;
  overflow-y: scroll;
}
.Chat {
  margin: 1rem 0.5rem;
  color: #333333;
  padding: 1rem;
  /* background: linear-gradient(90deg, #1986d8, #7b9cc2); */
  background: #f0f0f0;
  max-width: 90%;
  text-align: left; 
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}
.Chat.is-user-msg {
  margin-left: auto;
  background: #6f33d8;
  color: #fff;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

@media (min-width: 576px) {
  .Chat {
    max-width: 60%;
  }
}
.ChatWindow {
    display: flex;
    flex-direction: column;
    height: 100vh;
}
.Message {
  background-color: #f0f0f0;
  border-top: 1px #d0d0d0 solid;
}
.Message__input {
  width: 85%;
  height: 0.5rem;
  margin: 1rem auto;
  padding: 1rem;
  background: #fff;
  color: #333333;
  border: 0;
  border-radius: 10px;
  font-size: 1rem;
  outline: 0;
}

.Message__input::-webkit-input-placeholder {
  font-style: italic;
}

.Message__input::-moz-placeholder {
  font-style: italic;
}

.Message__input:-ms-input-placeholder {
  font-style: italic;
}

.Message__input::-ms-input-placeholder {
  font-style: italic;
}

.Message__input::placeholder {
  font-style: italic;
}

.Submit__button {
  width: 10%;
  border: none;
  color: #8f8f8f;
  font-size: 1rem;
  margin-top: auto;
  background: none;
  padding: 1rem;
  text-align: center;

}

/* not small devices  */
@media (max-width: 1200px) {
  .Submit__button {
    display: none;
  }
}
.Sidebar {
  width: 80px;
  background-color: #fff;
  height: 100%;
  border-right: 1px solid rgba(189, 189, 192, 0.1);
  -webkit-transition: width 0.3s;
  transition: width 0.3s;
  overflow-y: scroll;
}

/* not small devices  */
@media (min-width: 576px) {
  .Sidebar {
    width: 420px;
  }
}

.User {
  display: flex;
  align-items: center;
  padding: 1rem;
}
.User:hover {
  background: rgba(147, 147, 147, 0.2);
  cursor: pointer;
}
.User__pic {
  width: 55px;
  border-radius: 50%;
}
.User__details {
  display: none;
  text-align: start;
  line-height: 1rem;
  border-bottom: 1px solid #dcdcdc;
}

/* not small devices  */
@media (min-width: 576px) {
  .User__details {
    display: block;
    padding: 0 0 1.2rem 1rem;
    width: 80%;
  }
  .User__details-name {
    margin: 0;
    color: #525252;
    font-size: 1rem;
    width: 100%;
    
  }
  .User__details-status {
    margin-top: 0.5rem;
    margin-bottom: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
  }
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

